<template>
  <div>
    <b-container>
      <b-row>
        
        <b-col cols="6">
          <h2 class="positions-list-title">Список позиций</h2>

          <!-- Поиск позиции -->
          <b-form-group class="search-form">
            <b-form-input
              class="search"
              v-model="searchPosition"
              placeholder="Поиск по позициям"
            ></b-form-input>
            <button
              v-if="searchPosition"
              class="search-delete-btn"
              @click="removeSearch"
            >
              &#10005;
            </button>
          </b-form-group>

          <!-- Список позиций -->
          <div v-if="filteredPositions.length">
            <b-list-group
              class="position-group"
              v-for="(position, index) in filteredPositions"
              :key="position.id"
            >
              <b-list-group-item class="position-group_item">
                {{ position.title }}
                <button class="remove-btn" @click="removeBtn(index)">
                  &#10005;
                </button>
              </b-list-group-item>
            </b-list-group>
          </div>


          <!-- Индикатор загрузки, можно проверить работу через setTimeout -->
          <loader v-else-if="loading" />

          <p v-else class="no-positions">В списке нет позиций</p>
        </b-col>

        <!-- Добавление новой позиции    -->
        <b-col cols="6">
          <h2 class="positions-list-title">Добавить новую позицию</h2>

          <addPosition @clean-search="cleanSearch" />
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script>
import { mapGetters, mapActions, mapMutations } from "vuex";
import addPosition from "./addPosition";
import loader from "./loader";

export default {
  data() {
    return {
      searchPosition: "",
      loading: true,
    };
  },
  computed: {
    ...mapGetters(["allPositions"]),
    filteredPositions() {
      return this.allPositions.filter((position) => {
        let inputText = this.searchPosition.toLowerCase()
        let searchText = position.title.toLowerCase().indexOf(inputText)
        // if (searchText !== -1) {
        //   return searchText !== -1;
        // } else this.loading = false    
        return (searchText !== -1) ? (searchText !== -1) : (this.loading = false)
      });
    },
  },
  methods: {
    ...mapActions(["getPositions"]),
    ...mapMutations(["removePosition"]),
    removeBtn(index) {
      return this.removePosition(index),
      this.loading = false;
    },
    removeSearch() {
      this.searchPosition = "";
    },
    cleanSearch() {
      this.searchPosition=""
    }
  },
  async mounted() {
      return this.getPositions();

      this.loading = false;
  },
  components: {
    addPosition,
    loader
  },
};
</script>

<style lang="scss" scoped>
.positions-list-title {
  font-size: 21px;
  font-family: "ProximaNova-Regular";
  margin: 20px 5px;
  text-align: left;
}
.position-group {
  text-align: left;
  margin: 0 auto;
  margin-bottom: 5px;
  position: relative;
  .list-group-item {
    padding: 10px 27px 10px 10px;
  }
  .position-group_item {
    font-family: "ProximaNova-Regular";
    font-weight: 500;
    background-color: #7faaf8;
    border: none;
    color: white;
    transition: all .3s;
  }
  .position-group_item:hover {
    font-family: "ProximaNova-SemiBold";
    font-weight: 600;
    background-color: #7faaf8;
    box-shadow: 0px 0px 7px 0px rgba(0, 0, 0, 0.75);
    -webkit-box-shadow: 0px 0px 7px 0px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 0px 0px 7px 0px rgba(0, 0, 0, 0.75);
    color: white;
    font-weight: 700;
    & .remove-btn {
      font-weight: bold;
      color: white;
      transition: all .2s;
    }
    & .remove-btn:hover {
      color: red;
    }
  }
  .remove-btn {
    position: absolute;
    top: 9px;
    right: 2px;
    color: white;
    border: none;
    outline: none;
    background-color: transparent;
    cursor: pointer;
  }
}
.search {
  padding: 24px 10px;
  margin-bottom: 10px;
  font-family: "ProximaNova-Regular";
  font-weight: 500;
}
.no-positions {
  margin-top: 23px;
  font-family: "ProximaNova-Regular";
  font-weight: 500;
  font-size: 18px;
}

.search-form {
  position: relative;
  .form-control {
    padding: 20px 27px 20px 10px;
  }
  .search {
    margin-bottom: 0;
  }
  .search-delete-btn {
    position: absolute;
    top: 9px;
    right: 2px;
    color: red;
    border: none;
    outline: none;
    background-color: transparent;
    cursor: pointer;
    &:hover {
      font-weight: bold;
    }
  }
}
</style>


