<template>
  <b-form class="new-position-form" @submit.prevent="submit">
    <b-form-group class="new-position-form_item">
      <b-form-input
        v-model="title"
        placeholder="Введите название позиции"
      ></b-form-input>
      <button v-if="title" class="delete-adding-btn" @click="removeAdding">
        &#10005;
      </button>
    </b-form-group>
    <b-button class="new-position-btn" type="submit">Добавить позицию</b-button>
  </b-form>
</template>

<script>
import { mapMutations } from "vuex";

export default {
  data() {
    return {
      title: "",
    };
  },
  methods: {
    ...mapMutations(["newPosition"]),
    submit() {
      return (
        this.newPosition({
          title: this.title,
        }),
        this.$emit('clean-search'),
        this.title = ""
      );
    },
    removeAdding() {
      this.title = "";
    },
  },
};
</script>

<style lang="scss" scoped>
.form-control {
  padding: 20px 27px 20px 10px;
}
.new-position-btn {
  font-family: "ProximaNova-Regular";
  font-weight: 500;
  background-color: #6672fb;
  padding: 15px 12px;
  color: #eaecee;
  border: none;
  outline: none;
  transition: all 0.2s;
  &:hover {
    color: #fff;
    box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.75);
    -webkit-box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.75);
  }
}
.new-position-form_item {
  position: relative;
  font-family: "ProximaNova-Regular";
  .delete-adding-btn {
    position: absolute;
    top: 9px;
    right: 2px;
    color: red;
    border: none;
    outline: none;
    background-color: transparent;
    cursor: pointer;
    transition: all 0.2s;
    &:hover {
      font-weight: bold;
    }
  }
}
</style>


